import { interfaces } from 'inversify'
import { flowdaServiceModule } from '@flowda-projects/flowda-services'
import { PrismaClient } from '@prisma/client-flowda'
import { flowdaSharedModule, PrismaClientSymbol } from '@flowda-projects/flowda-shared'
import { flowdaSharedNodeModule } from '@flowda-projects/flowda-shared-node'
import { flowdaServiceTrpcServerModule } from '@flowda-projects/flowda-services-trpc-server'

const prisma = new PrismaClient({
  log: [
    // 'query',
    'info',
    'warn',
    'error',
  ],
})

export function loadModule(container: interfaces.Container) {
  container.bind<PrismaClient>(PrismaClientSymbol).toConstantValue(prisma)
  container.load(flowdaSharedModule)
  container.load(flowdaSharedNodeModule)
  container.load(flowdaServiceTrpcServerModule)
  container.load(flowdaServiceModule)
}
