import { inject, injectable } from 'inversify'
import { TrpcService } from './trpc.service'
import type { LoggerService } from '@nestjs/common'
import { z } from 'zod'
import { UserService } from '@flowda-projects/flowda-services'

@injectable()
export class UserRouter {
  private readonly logger

  constructor(
    @inject(TrpcService) private trpc: TrpcService,
    @inject(UserService) private userService: UserService,
    @inject('Factory<Logger>') loggerFactory: (name: string) => LoggerService,
  ) {
    this.logger = loggerFactory(UserRouter.name)
  }

  userRouter = this.trpc.router({
    validate: this.trpc.procedure
      .input(
        z.object({
          username: z.string(),
          password: z.string(),
        }),
      )
      .output(
        z.object({
          username: z.string(),
          refresh_token: z.string(),
          access_token: z.string(),
        }),
      )
      .query(async ({ input }) => {
        const ret = await this.userService.validate(input.username, input.password)
        return ret
      }),
  })
}
