import { Controller, Get } from '@nestjs/common'

import { SchemaService } from '@flowda-projects/flowda-shared'
import { DynamicTableDefService } from '@flowda-projects/flowda-services'

@Controller('/apps')
export class SchemaController {
  constructor(private readonly schema: SchemaService, private readonly defService: DynamicTableDefService) {}

  @Get('/getSchema')
  async getSchema() {
    const zodSchema = this.schema.getSchema()
    const dynamicSchema = await this.defService.getSchema()
    const ret = Object.assign({}, zodSchema, dynamicSchema)
    return ret
  }
}
